<template>
	<div class="adress">
		<img class="logo" src="../images/address_logo.png">
		<img class="title_img" src="../images/title.png" alt="">
		<div class="info">
			<div class="name">
				<label for="name">姓名：</label>
				<input type="text" id="name" placeholder="收货人姓名" v-model="name">
			</div>
			<div class="phone">
				<label for="phone">手机：</label>
				<input type="number" id="phone" placeholder="收货人联系方式" v-model="phone">
			</div>
			<div class="address">
				<label for="address">地址：</label>
				<input type="text" id="address" placeholder="填写收货地址" v-model="address">
			</div>
		</div>
		<div class="submit" @click="submit">立即领取</div>
	</div>
</template>
<script>
import { expressage } from '../api/api'
import { Toast } from 'mint-ui'
export default {
	data() {
		return {
			name: '',
			phone: '',
			address: ''
		}
	},
	methods: {
		submit() {
			var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
			if (this.name === '') {
				Toast({
					message: '请输入姓名',
					position: 'middle',
					duration: 2000
				});
			} else {
				if (phoneReg.test(this.phone)) {
					if (this.address === '') {
						Toast({
							message: '请输入收货地址',
							position: 'middle',
							duration: 2000
						});
					} else {
						let params = {
							userId: '',
							consignee: this.name,
							tel: this.phone,
							address: this.address,
							flag: 0,
							mobileType: '3'
						}
						expressage(params).then(res => {
							if (res.code === 200) {
								Toast({
									message: '我们已收到您的信息',
									position: 'middle',
									duration: '2000'
								})
								this.name = ''
								this.phone = ''
								this.address = ''
							} 
						})
					}
				} else {
					Toast({
						message: '请输入正确的手机号码',
						position: 'middle',
						duration: 2000
					});
				}
			}
		}
	},
	mounted() {

	}
}
</script>
<style lang="scss" rel="stylesheet/scss">
	.adress {
		height: 100%;
		background: #ffb463;
		position: relative;
		z-index: 11;
		.logo {
			position: absolute;
			top: 0;
			left: 0.2rem;
			width: 1.04rem;
		}
		.title_img {
			display: block;
			margin: 0 auto;
			width: 3.47rem;
			height: auto;
			margin-top: 0.73rem;
		}
		.info {
			margin-top: 0.37rem;
			background: #fff1ca;
			padding: 0.4rem 0 0.4rem;
			margin: 0.37rem auto 0;
			width: 6rem;
			border-radius: 0.1rem;
			div {
				margin: 0.4rem 0 0 0.46rem;
				label {
					font-size: 0.26rem;
					vertical-align: middle;
				}
				input {
					padding-left: 0.3rem;
					width: 4rem;
					height: 0.8rem;
					border-radius: 0.1rem;
					outline: none;
				}
			}
			.name {
				margin-top: 0;
			}
		}
		.submit {
			width: 5rem;
			background-image: linear-gradient(0deg, #e33717 0%, #ef4728 50%, #f95436 100%);
			height: 0.8rem;
			line-height: 0.8rem;
			text-align: center;
			color: #fff;
			font-size: 0.34rem;
			margin: 0 auto;
			margin-top: 1.5rem;
			border-radius: 0.1rem;
		}
	}
</style>



